import { Injectable } from '@angular/core';
import { HttpClient,  HttpHeaders, HttpParams } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UserServiceService {
  isLogin = false;
  base = 'http://localhost:3300/api';
  constructor(private http : HttpClient) { }

  checkIsLogin(){
    return this.isLogin;
  }

  loggedIn(){
    this.isLogin = true;
    return this.isLogin;
  }

  loggedOut(){
    this.isLogin = false;
    return this.isLogin;
  }

  submitNewUserDetails(data : any){
    const body = {
      first_name : data.first_name,
      last_name : data.last_name,
      pwd : data.pwd,
      email : data.email,
      update_dt : new Date()
    }

    return this.http.post(this.base+'/postNewUserDetails', body, {withCredentials : true});
  }
}
